class IndexPage extends React.Component{
 
    constructor(){
        super()
        this.state= {
            invoices:testData,
            crrInvoice:null
        }
        this.setActive= this.setActive.bind(this)
    }
    async setActive(r, e){
        const {apiKey, adminId} = this.state;
        const res = await fetch(
            `https://moneybirdhandlerproxy.azurewebsites.net/api/MoneyBirdProxy`,
            {
                mode:"cors",
                method:"POST",
                body: JSON.stringify({
                    method:"GET",
                    path:`${adminId}/sales_invoices/${r.id}/download_pdf.json`,
                    key: apiKey
                })
            }
        )
        const pdf = await res.text();
        console.log({pdf, res})
        this.setState({crrInvoice:pdf})
        
    }
    componentDidMount(){
        this.microsoftTeams = window? window.microsoftTeams: null;
        if(this.microsoftTeams){
            this.microsoftTeams.initialize();
            this.microsoftTeams.getContext(async ({entityId}) =>{
                console.log({entityId})
                const {apiKey, adminId, contact:{value :contactId}} = JSON.parse(entityId);
                const res = await fetch(
                    `https://moneybirdhandlerproxy.azurewebsites.net/api/MoneyBirdProxy`,
                    {
                        mode:"cors",
                        method:"POST",
                        body: JSON.stringify({
                            method:"GET",
                            path:`${adminId}/sales_invoices.json?filter=contact_id:${contactId}`,
                            key: apiKey
                        })
                    }
                )
                const json = await res.json();
                this.setState({
                    invoices: json,
                    adminId,
                    apiKey
                })
            })
        }
    }
    render(){
        return (
        <div>
            <h1>Hello World</h1>
            <div>
                <DataTable
                    columns={columns}
                    data={testData}
                    onRowClicked={this.setActive}
                    pointerOnHover={true}
                    highlightOnHover={true}
                />
            </div>
        </div>)
    }
  
}

export default IndexPage;
